body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.carouselImg{
    float: right;
    width: 70%;
    height:75vh ;
}
.carouselText{
     float: left;
     font-weight: 400;
     line-height: 1.5;
     letter-spacing:-.026em; 
     font-size: 34px; 
     padding-top: 10vh;
}
#container{
    flex-direction:column;
    margin-top: 10%;
    width: 100%;
    height:80vh;
    border-top-width: 5px;
}
.stats{
    
    margin:20px 50px;
    display:block;
    font-size: 16px;
    line-height: 1.7;
    letter-spacing: 0;
    font-weight: 300;
    color: #565b73;
    width:90%;
    
}
.title{
    font-size: 30px;
}
.row{
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px
}
.lead{
    justify-content: center;
    text-align: center;
    margin-top:3%;
}
.stats li {
    position: relative;
    border-radius: 5px;
    box-shadow: 0 5px 25px rgb(0 0 0 / 10%);
    border: 1px solid #f5f5f5;
    font-size: calc(11.53488px + .34884vw);
    color: #1f304c;
    font-weight: 400;
    line-height: 1.5;
    max-width: 360px;
    padding: 80px 60px 42px;
    text-align: center;
    margin: 40px 0;
    width:80%;
    background-color: silver;
height: 60%;}

.stats__icon.-red {
    background-color: #f22f46;
}


.stats__icon{
display: flex;
align-items: center;
justify-content: center;
content: "";
height: 90px;
width: 90px;
top: -45px;
left: 50%;
border-radius: 50%;
position: absolute;
margin-left: -45px;
}
.cards{
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    display: flex;
    justify-content: center;
    flex: 0 0 33.33333%;
    max-width: 33.33333%;
    height:60vh;

}
stats__card{
    width:25%;
}

.left{
    float:left;
    height: 80vh;
    background-color: aqua;
    width:20%;
    overflow: auto;
}
.right{
    float:left;
    height: auto;
    background-color: rgb(202, 202, 202);
    width:80%;
}
.videoGrid {
    display: flex;
    justify-content: center;
    height: 80vh;
    align-items: center;
    flex-wrap: wrap;
  }
  video {
    display: block;
    flex: 1 1;
    object-fit: cover;
    max-width: 600px; 
   
    
  }
  .main {
    height: 100vh;
    display: flex;
    
  }
  
  
  .roomScreen{
    height:100vh;
    width:100vw;
    position: fixed;
    display: flex;
    border-width: medium;
    border-color: deepskyblue;
    background-color: rgba(9,40,68,255);
  }
  .videoChatBlock{
    
    width:80vw;
    height:100vh;
   
  }
  .chatBlock{
  
    width:20vw;
    height:100vh;
    background-color: rgba(9,40,68,255);
    border-width: medium;
    border-color: deepskyblue;
  }
  
  .myVideoGrid{
    float:left;
    height:'40vh';
    position: fixed;
    bottom: 0;
  }
  .mainControlsBlock {
    display: flex;
    margin-left: 35%;
    height:60px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; 
    position: fixed;
    bottom: 2%;
   
  }
  .mainControlsButton {
    
    display: flex;
    flex-direction: column;
    cursor: pointer;
    /* padding: 10px 20px; */
    width:65px;
    height:65px;
    justify-content: center;
    align-items: center;
    min-width: 50px;
    transition: all 0.3s ease-in-out;
    border-radius: 50%;
    margin: 5px;
    color:white;
    background-color: rgb(0, 195, 255);
   
   
  }
  .mainControlsButton span {
    margin-top: 10px;
    display: block;
    font-size: small;
  }
  .mainControlsButton:hover {
    background-color: #34383b;
  }
  .mainControlsButton i {
    font-size: 40px;
  }
  
  
  
  .header {
    color: white;
    font-size: large;
    font-weight: bold;
    text-align: center;
    padding: 20px;
    border-bottom: 2px solid #3d3d42;
  } 
  .chatWindow {
    height:84vh;
    flex-grow: 1;
    overflow: auto;
    padding-top: 10px; 
    background-color:rgba(9,40,68,255);
    border-width: medium;
    border-color: deepskyblue;}
  .messageContainer {
    padding: 5px 5px;
    display: flex;
    overflow: auto;
    background-color: gold;
  }
  .messageContainer input {
    flex-grow: 1;
    background-color: white;
    border: none;
    color: blue;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    outline: none;
    width: 20vw;
    height:40px;
  }
  
  
  #liveMessage li {
   
    border-radius: 5px;
    padding: 8px 8px 8px 8px;
    margin-top: 5px;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    color: black;
    display: flex;
    flex-direction: column;
    align-self: flex-end;
    /* max-width: 80%; */
  
  }
  
  #divmsg{
    padding:10px;
  }
  #myModal {
    /* display: none; Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    right: 0;
    
    width:20vw;
    overflow: auto; /* Enable scroll if needed */
    
    /* background-color:rgba(9,40,68,255); */
    
  }
  
  
  /* The Close Button */
  .close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }
  #chatBoxIcon{
    position: absolute;
    top: 10%;
    right: 2%;
  }
  #u{
    padding-top: 15%;
  }
